.logo {
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 4rem;
    font-weight: bold;
    font-family: "Arial Rounded MT Bold", sans-serif;
    color: var(--main-accent);
    user-select: none;
}

.logo-img {
    max-height: 6rem;
    object-fit: scale-down;

    filter: invert(36%) sepia(77%) saturate(3477%) hue-rotate(217deg) brightness(95%) contrast(84%)
}