.video-call-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    gap: 15px;
    width: 100%;
}

.email-input {
    margin-bottom: 15px;
}

.email-input input {
    padding: 10px;
    font-size: 16px;
    width: 100%;
    max-width: 400px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.video-wrapper {
    display: flex;
    justify-content: center;
    gap: 10px;
    width: 100%;
    height: 60vh;
}

.local-video, .remote-video {
    width: 48%;
    height: 100%;
    background-color: #000;
    border: 2px solid #ddd;
    border-radius: 8px;
}

.controls {
    display: flex;
    gap: 10px;
}

.controls button {
    padding: 12px 20px;
    font-size: 16px;
    cursor: pointer;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
}
