/* Center the content */
.home-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    color: #333;
    font-family: Arial, sans-serif;
}

.title {
    font-size: 2em;
    color: #1a73e8;
    margin-bottom: 20px;
    text-align: center;
}

.lesson-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.lesson-card {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.lesson-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

.lesson-card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
    margin-bottom: 10px;
}

.lesson-card-header h2 {
    font-size: 1.5em;
    color: #333;
    margin: 0;
}

.lesson-card-header span {
    font-size: 0.9em;
    color: #666;
}

.lesson-card-body p {
    margin: 5px 0;
}

.lesson-card-body strong {
    color: #1a73e8;
}

.no-lessons {
    font-size: 1.2em;
    color: #666;
    text-align: center;
}
