/* General container for the calendar */
.calendar-container {
    max-width: 900px;
    margin: 40px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Calendar Button for Adding a Lesson */
  .add-lesson-button {
    background-color: #00bcd4;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-bottom: 20px;
  }
  
  .add-lesson-button:hover {
    background-color: #008c9e;
  }
  
  /* FullCalendar general styles */
  .fc {
    font-family: 'Arial', sans-serif;
    background-color: #ffffff;
    border-radius: 8px;
  }
  
  /* Toolbar (header) customization */
  .fc-toolbar {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .fc-toolbar .fc-button-group .fc-button {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 5px 10px;
    font-size: 14px;
    cursor: pointer;
  }
  
  .fc-toolbar .fc-button-group .fc-button:hover {
    background-color: #45a049;
  }
  
  .fc-toolbar-title {
    font-size: 20px;
    font-weight: bold;
    color: #333;
  }
  
  /* Calendar Day/Week Grid styling */
  .fc-daygrid-day {
    border: 1px solid #ddd;
  }
  
  .fc-daygrid-day-top {
    text-align: right;
    padding: 5px;
    font-size: 12px;
  }
  
  .fc-day-past {
    background-color: #f1f1f1;
  }
  
  .fc-day-today {
    background-color: #ffeb3b;
    font-weight: bold;
  }
  
  /* Event styles */
  .fc-event {
    background-color: #00bcd4;
    border: none;
    color: white;
    border-radius: 4px;
    padding: 5px;
    font-size: 12px;
    cursor: pointer;
  }
  
  .fc-event:hover {
    background-color: #008c9e;
  }
  
  /* Modal Styles */
  .modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
  }
  
  .modal-content {
    max-width: 400px;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }

  .modal-close {
    cursor: pointer;
    font-size: 20px;
    color: #888;
    align-self: flex-end;
    margin-bottom: 10px;
  }
  
  .modal-content h2 {
    margin-top: 0;
    text-align: center;
    font-size: 1.5em;
    color: #333;
    margin-bottom: 20px;
  }
  
  .modal-content input[type="datetime-local"],
  .modal-content input[type="number"],
  .modal-content textarea,
  .modal-content .select {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 1em;
}

.modal-content .select {
  max-height: 5em;
  overflow: scroll;
}
  
  .modal-content button {
    background-color: #00bcd4;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    width: 100%;
  }
  
  .modal-content button:hover {
    background-color: #008c9e;
  }
  
  .modal-content button + button {
    margin-top: 10px;
  }

  .submit-btn {
    width: 100%;
    background-color: #00c4cc;
    color: white;
    padding: 10px;
    margin-top: 10px;
    border: none;
    border-radius: 4px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .submit-btn:hover {
    background-color: #009ca1;
  }

  .delete-btn {
    width: 100%;
    background-color: #ff4b4b !important;
    color: white;
    padding: 10px;
    margin-top: 10px;
    border: none;
    border-radius: 4px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .delete-btn:hover {
    background-color: #cc3a3a !important;
  }

  .custom-tooltip {
    max-width: 250px;
    background-color: #333;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    font-size: 14px;
  }
  
  .tooltip-content h3 {
    margin: 0 0 10px;
    font-size: 16px;
    color: #ffd700;
  }
  
  .tooltip-content p {
    margin: 5px 0;
  }
  
  .calendar-event {
    cursor: pointer;
  }
  
  
  /* Loader styles (optional if you want a loader while fetching data) */
  .loader {
    border: 4px solid #f3f3f3;
    border-radius: 50%;
    border-top: 4px solid #00bcd4;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: 20px auto;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  