/* Container Styles */
.resources-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  
  /* Grid Layout */
  .resources-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
  
  /* Card Layout */
  .metronome-card,
  .tuner-card,
  .timer-card {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 20px;
    width: 300px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  /* Beat Indicator */
  .beat-indicator {
    width: 50px;
    height: 50px;
    margin: 20px auto;
    background: linear-gradient(135deg, #3498db, #9b59b6);
    border-radius: 50%;
    transition: transform 0.1s ease;
  }
  
  .beat-active {
    transform: scale(1.3);
  }
  
  /* Slider Styles */
  .bpm-slider {
    width: 100%;
    margin: 15px 0;
    appearance: none;
    height: 5px;
    border-radius: 5px;
    background: linear-gradient(90deg, #3498db, #9b59b6);
  }
  
  /* Timer Display */
  .timer-display {
    font-size: 1.5em;
    margin-bottom: 10px;
  }
  
  /* Progress Bar */
  .progress-bar {
    width: 100%;
    height: 10px;
    background: #e0e0e0;
    border-radius: 5px;
    overflow: hidden;
  }
  
  .progress {
    height: 100%;
    background: linear-gradient(90deg, #3498db, #9b59b6);
  }
  
  /* Input Styles */
  .time-input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
  }
  
  /* Button Styles */
  .metronome-button,
  .tuner-button,
  .timer-button {
    padding: 10px 20px;
    border: none;
    border-radius: 10px;
    background: linear-gradient(135deg, #3498db, #9b59b6);
    color: #fff;
    font-size: 1em;
    cursor: pointer;
    transition: background 0.2s ease, transform 0.1s ease;
  }
  
  .metronome-button:hover,
  .tuner-button:hover,
  .timer-button:hover {
    background: linear-gradient(135deg, #2980b9, #8e44ad);
    transform: translateY(-2px);
  }
  
  .metronome-button:active,
  .tuner-button:active,
  .timer-button:active {
    transform: translateY(0);
  }
  