.google-role-select {
    text-align: center;
}

.role-selection {
    display: flex;
    justify-content: space-around;
    margin: 64px;
}

.role-option {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    font-size: 200%;
    font-weight: bold;

    cursor: pointer;
}

.role-option img {
    object-fit: contain;
    max-width: 100%;
    filter: invert(36%) sepia(77%) saturate(3477%) hue-rotate(217deg) brightness(95%) contrast(84%);
}

.role-option .img-container {
    position: relative;
    width: 12vw;
    padding: 32px;
}

.img-container::before {
    content: "";
    position: absolute;
    top: 5%;
    left: 5%;
    width: 90%;
    height: 90%;
    background: white;
    clip-path: circle(50%);

    transition-property: top, left, width, height;
    transition-duration: 0.15s;
    transition-timing-function: ease-out;
}

.role-option:hover .img-container::before {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}