/* General Container */
.profile-container {
    max-width: 600px;
    margin: 0 auto;
    text-align: center;
    padding: 20px;
    font-family: Arial, sans-serif;
}

.students-section {
    margin-top: 20px;
}

.students-list {
    list-style-type: none;
    padding: 0;
}

.students-list li {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    font-size: 16px;
}

.add-student-btn {
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #3498db;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.add-student-btn:hover {
    background-color: #2980b9;
}

/* Modal */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 10px;
    width: 300px;
    text-align: center;
    position: relative;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 20px;
    cursor: pointer;
    color: #888;
}

.close-modal:hover {
    color: black;
}

.student-input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
}

.submit-btn {
    padding: 10px 20px;
    background-color: #2ecc71;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.submit-btn:hover {
    background-color: #27ae60;
}

.remove-btn {
    margin-left: 10px;
    padding: 5px 10px;
    background-color: #e74c3c;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.remove-btn:hover {
    background-color: #c0392b;
}

/* Log Out Button */
#log-out {
    margin-top: 20px;
    padding: 10px 20px;
    background: none;
    border: 1px solid #ddd;
    border-radius: 5px;
    color: #555;
    cursor: pointer;
}

#log-out:hover {
    background-color: #f5f5f5;
}
