.notes-container {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.note-item {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #f9f9f9;
}

button {
    padding: 10px 15px;
    margin-top: 10px;
    cursor: pointer;
}

.note-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
}

.note-form input,
.note-form textarea {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.note-form button {
    padding: 10px;
    cursor: pointer;
}
